import * as React from "react";
import * as ReactDOM from "react-dom";
import {
  JSWindow,
  WindowState,
  WindowStyle,
  WindowInfo,
  SplitView,
  SplitType,
  ListView,
  TreeView,
  TreeItem,
  TreeItemStyle
} from "@jswf/react";

function App() {
  const frame = React.createRef<JSWindow>();
  const [info, setInfo] = React.useState<WindowInfo | null>(null);
  const [type, setType] = React.useState<SplitType>("ew");

  let listViewRef = React.useRef<ListView>(null);
  const [message, setMessage] = React.useState("");
  const [message2, setMessage2] = React.useState();
  const treeView = React.useRef<TreeView>(null);
  let count = 1;
  return (
    <>
      {/* --------  ------------*/}
      <JSWindow ref={frame} title="Window1" x={50} y={100}>
        
      </JSWindow>

      {/* --------  ------------*/}
      <JSWindow
        title="Window2"
        width={600}
        height={500}
        windowStyle={~WindowStyle.CLOSE}
      >
        
        <br />
        windowStyle
        <JSWindow
          title="ChildWindow"
          overlapped={false}
          width={200}
          height={200}
        >
          overlappedfalsetrue
        </JSWindow>
      </JSWindow>

      {/* --------  ------------*/}
      <JSWindow title="" y={50} onUpdate={p => setInfo(p)}>
        <pre>
          {info &&
            JSON.stringify(
              info,
              ["realX", "realY", "realWidth", "realHeight"],
              " "
            )}
        </pre>
      </JSWindow>

      {/* --------  ------------*/}
      <JSWindow
        width={500}
        height={400}
        title=""
        clientStyle={{ display: "flex", flexDirection: "column" }}
      >
        {/*  */}
        <div style={{ borderBottom: "solid 2px" }}>
          <button onClick={() => setType("we")}>WE</button>
          <button onClick={() => setType("ew")}>EW</button>
          <button onClick={() => setType("ns")}>NS</button>
          <button onClick={() => setType("sn")}>SN</button>
        </div>
        {/* () */}
        <SplitView type={type} style={{ position: "relative", flex: 1 }}>
          <div style={{ height: "100%" }}></div>
          <div style={{ height: "100%", backgroundColor: "rgb(230,255,255)" }}>
            
          </div>
        </SplitView>
      </JSWindow>

      {/* -------- ListView ------------*/}
      <JSWindow x={100} y={0} width={600} title="ListView">
        <SplitView>
          <div>
            <button
              onClick={() => {
                //
                listViewRef.current!.addItem([
                  count++,
                  <>
                    <b>
                      <br />
                    </b>
                  </>,
                  0,
                  1
                ]);
              }}
            >
              
            </button>
            <br />
            DOM
            <br />
            <br />
            <button
              onClick={() => {
                const items = listViewRef.current!.getSelectItems();
                items.sort((a, b) => b - a);
                for (const item of items) listViewRef.current!.removeItem(item);
              }}
            >
              
            </button>
            <br />
            
            <br />
            <br />
            {message}
          </div>
          <ListView
            ref={listViewRef}
            onItemClick={(row, col) => {
              //
              const item = listViewRef.current!.getItem(row, col);
              //
              if (item)
                setMessage(`${item.toString()}`);
            }}
          >
            <div>
              <div data-type="number">No</div>
              <div data-width={100}>
                
                <br />
                
              </div>
              <div data-type="number"></div>
              <div data-type="number"></div>
            </div>
            <div>
              <div>{count++}</div>
              <div></div>
              <div>5</div>
              <div>10</div>
            </div>
            <div>
              <div>{count++}</div>
              <div></div>
              <div>10</div>
              <div>40</div>
            </div>
            <div>
              <div>{count++}</div>
              <div></div>
              <div>18</div>
              <div>120</div>
            </div>
            <div>
              <div>{count++}</div>
              <div></div>
              <div>30</div>
              <div>380</div>
            </div>
            <div>
              <div>{count++}</div>
              <div></div>
              <div>40</div>
              <div>700</div>
            </div>
          </ListView>
        </SplitView>
      </JSWindow>

      <JSWindow width={600} title="TreeView">
        <SplitView>
          <div>
            <button onClick={()=>{
              const item = treeView.current!.getSelectItem();
              if(item)
                item.addItem({label:""});
            }}></button><br/><br/>
            <button onClick={()=>{
              const item = treeView.current!.getSelectItem();
                if(item)
                  item.remove();
              }}></button><br/><br/>
            <button onClick={()=>{
              const items = treeView.current!.getCheckItems();
              for(const item of items)
                  item.remove();
              }}></button><br/><br/>
            {message2}
          </div>

          <TreeView
            itemStyle={TreeItemStyle.CHECKBOX}
            ref={treeView}
            onItemClick={(item)=>setMessage2(item.getLabel())}>
            <TreeItem label="Root">
              <TreeItem label="Data2">
                <TreeItem label="Data3"><TreeItem label="Data4"></TreeItem></TreeItem>
              </TreeItem>
              <TreeItem label={<><b></b></>}>
                <TreeItem label={<><br/></>}></TreeItem>
              </TreeItem>
              <TreeItem label={<><input defaultValue="TextBox"/></>}/>
            </TreeItem>
          </TreeView>
        </SplitView>
      </JSWindow>

      {/* --------  ------------*/}
      <button
        onClick={() => {
          frame.current!.foreground();
          frame.current!.setWindowState(WindowState.NORMAL);
        }}
      >
        Window1
      </button>
    </>
  );
}

ReactDOM.render(<App />, document.getElementById("root") as HTMLElement);
